___CSS_LOADER_EXPORT___.push([module.id, ".isCurrent{color:#00f;z-index:1;margin-top:1rem}", "",{"version":3,"sources":["webpack://./resources/js/components/GameCard.vue"],"names":[],"mappings":"AAwKA,WACC,UAAU,CACV,SAAW,CACX,eACD","sourcesContent":["<template>\n  <div\n    v-if=\"isShowing\"\n    ref=\"interactElement\"\n    :class=\"{\n      isAnimating: isInteractAnimating,\n      isCurrent: isCurrent,\n    }\"\n    class=\"card\"\n    :style=\"{ transform: transformString }\"\n  >\n    <h3 class=\"cardTitle\">{{ card }}</h3>\n\t <!-- change the prop (card / isCurrent). Adding questions data to cards -->\n  </div>\n</template>\n\n<script>\nimport interact from \"interactjs\";\nconst ACCEPT_CARD = \"cardAccepted\";\nconst REJECT_CARD = \"cardRejected\";\nconst SKIP_CARD = \"cardSkipped\";\n\nexport default {\n  static: {\n    interactMaxRotation: 15,\n    interactOutOfSightXCoordinate: 180,\n    interactOutOfSightYCoordinate: 600,\n    interactYThreshold: 150,\n    interactXThreshold: 100,\n  },\n\n  props: {\n    card: {\n      type: String,\n      required: true,\n    },\n    isCurrent: {\n      type: Boolean,\n      required: true,\n    },\n  },\n\n  data() {\n    return {\n      isShowing: true,\n      isInteractAnimating: true,\n      isInteractDragged: null,\n      interactPosition: {\n        x: 0,\n        y: 0,\n        rotation: 0,\n      },\n    };\n  },\n\n  computed: {\n    transformString() {\n      if (!this.isInteractAnimating || this.isInteractDragged) {\n        const { x, y, rotation } = this.interactPosition;\n        return `translate3D(${x}px, ${y}px, 0) rotate(${rotation}deg)`;\n      }\n\n      return null;\n    },\n  },\n\n  mounted() {\n    const element = this.$refs.interactElement;\n\n    interact(element).draggable({\n      onstart: () => {\n        this.isInteractAnimating = false;\n      },\n\n      onmove: (event) => {\n        const { interactMaxRotation, interactXThreshold } =\n          this.$options.static;\n        const x = this.interactPosition.x + event.dx;\n        const y = this.interactPosition.y + event.dy;\n\n        let rotation = interactMaxRotation * (x / interactXThreshold);\n\n        if (rotation > interactMaxRotation) rotation = interactMaxRotation;\n        else if (rotation < -interactMaxRotation)\n          rotation = -interactMaxRotation;\n\n        this.interactSetPosition({ x, y, rotation });\n      },\n\n      onend: () => {\n        const { x, y } = this.interactPosition;\n        const { interactXThreshold, interactYThreshold } = this.$options.static;\n        this.isInteractAnimating = true;\n\n        if (x > interactXThreshold) this.playCard(ACCEPT_CARD);\n        else if (x < -interactXThreshold) this.playCard(REJECT_CARD);\n        //else if (y > interactYThreshold) this.playCard(SKIP_CARD);\n        else this.resetCardPosition();\n      },\n    });\n\n  },\n\n  beforeUnmount() {\n    interact(this.$refs.interactElement).unset();\n  },\n\n  methods: {\n    hideCard() {\n      setTimeout(() => {\n        this.isShowing = false;\n        this.$emit(\"hideCard\", this.card);\n      }, 300);\n    },\n\n    playCard(interaction) {\n      const {\n        interactOutOfSightXCoordinate,\n        interactOutOfSightYCoordinate,\n        interactMaxRotation,\n      } = this.$options.static;\n\n      this.interactUnsetElement();\n\n      switch (interaction) {\n        case ACCEPT_CARD:\n          this.interactSetPosition({\n            x: interactOutOfSightXCoordinate,\n            rotation: interactMaxRotation,\n          });\n          this.$emit(ACCEPT_CARD);\n          break;\n        case REJECT_CARD:\n          this.interactSetPosition({\n            x: -interactOutOfSightXCoordinate,\n            rotation: -interactMaxRotation,\n          });\n          this.$emit(REJECT_CARD);\n          break;\n        //   case SKIP_CARD:\n        //     this.interactSetPosition({\n        //       y: interactOutOfSightYCoordinate\n        //     });\n        //     this.$emit(SKIP_CARD);\n        //     break;\n      }\n\n      this.hideCard();\n    },\n\n    interactSetPosition(coordinates) {\n      const { x = 0, y = 0, rotation = 0 } = coordinates;\n      this.interactPosition = { x, y, rotation };\n    },\n\n    interactUnsetElement() {\n      interact(this.$refs.interactElement).unset();\n      this.isInteractDragged = true;\n    },\n\n    resetCardPosition() {\n      this.interactSetPosition({ x: 0, y: 0, rotation: 0 });\n    },\n  },\n};\n</script>\n\n<style>\n.isCurrent{\n\tcolor:blue;\n\tz-index: +1;\n\tmargin-top: 1rem\n}\n</style>\n"],"sourceRoot":""}]);
